import React, {Component} from 'react'
import {Card, Button} from 'antd'
import ReactEcharts from 'echarts-for-react'

/*

 */
export default class Bar extends Component {

  state = {
    sales: [5, 20, 36, 10, 10, 20], // 
    stores: [6, 10, 25, 20, 15, 10], // 
  }

  update = () => {
    this.setState(state => ({
      sales: state.sales.map(sale => sale + 1),
      stores: state.stores.reduce((pre, store) => {
        pre.push(store-1)
        return pre
      }, []),
    }))
  }

  /*
  
   */
  getOption = (sales, stores) => {
    return {
      title: {
        text: 'ECharts '
      },
      tooltip: {},
      legend: {
        data:['', '']
      },
      xAxis: {
        data: ['', '', '', '', '']
      },
      yAxis: {},
      series: [{
        name: '',
        type: 'bar',
        data: sales,
        itemStyle: {
          normal: {
            color: "#ee8572"
          }
        }
      }, {
        name: '',
        type: 'bar',
        data: stores,
        itemStyle: {
          normal: {
            color: "#63b7af"
          }
        }
      }]
    }
  }

  getOption2 = () => {
    return {

      tooltip: {
        trigger: 'axis',
        axisPointer: {            // 
          type: 'shadow'        // 'line' | 'shadow'
        }
      },
      legend: {
        data: ['', '', '', '', '']
      },
      grid: {
        left: '3%',
        right: '4%',
        bottom: '3%',
        containLabel: true
      },
      xAxis: {
        type: 'value'
      },
      yAxis: {
        type: 'category',
        data: ['', '', '', '', '', '', '']
      },
      series: [
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            show: true,
            position: 'insideRight'
          },
          data: [320, 302, 301, 334, 390, 330, 320]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            show: true,
            position: 'insideRight'
          },
          data: [120, 132, 101, 134, 90, 230, 210]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            show: true,
            position: 'insideRight'
          },
          data: [220, 182, 191, 234, 290, 330, 310]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            show: true,
            position: 'insideRight'
          },
          data: [150, 212, 201, 154, 190, 330, 410]
        },
        {
          name: '',
          type: 'bar',
          stack: '',
          label: {
            show: true,
            position: 'insideRight'
          },
          data: [820, 832, 901, 934, 1290, 1330, 1320]
        }
      ]
    };
  }
  
  render() {
    const {sales, stores} = this.state
    return (
      <div>
        <Card>
          <Button type='primary' onClick={this.update}></Button>
        </Card>

        <Card title=''>
          <ReactEcharts 
            option={this.getOption(sales, stores)} 
            
          />
        </Card>

        <Card title=''>
          <ReactEcharts option={this.getOption2()} />
        </Card>
      </div>
    )
  }
}