import './order.less'
import React, {Component} from 'react'
import {
  Card,
  Select,
  Input,
  Button,
  Icon,
  Table,
  message,
  Modal
} from 'antd'

import LinkButton from '../../components/link-button'
import {reqOrders, reqSearchOrders,reqOrderDelivery,reqOrderEnd} from '../../api'
import {PAGE_SIZE} from '../../utils/constants'
import memoryUtils from "../../utils/memoryUtils";

const Option = Select.Option


export default class OrderHome extends Component {
 
  state = {
    total: 0, // 
    orders: [], // 
    loading: false, // 
    searchContent: '', // 
    searchType: 'orderNo', // 
  }


  /*
  table
   */
  initColumns = () => {
    this.columns = [
      {
        width: 140,
        title: '',
        dataIndex: 'orderNo',
      },
      {
        width: 100,
        title: '',
        dataIndex: 'totalPrice',
        render: (totalPrice) => '' + totalPrice/100 // , 
      },
      {
        width:110,
        title: '',
        dataIndex: 'orderStatusName',
        // render:(orderStatus) => {
        //   switch(orderStatus){
        //     case 0:return "";
        //     case 10:return "";
        //     case 20:return "";
        //     case 30:return "";
        //     case 40:return "";
        //   }
        // }
      },
      {
        width:100,
        title: '',
        dataIndex: 'paymentType',
        render:(paymentType) => paymentType==1?"":""
      },
      {
        width:150,
        title: '',
        dataIndex: 'createTime',
        render:(createTime) => createTime.substring(0,10)+" "+createTime.substring(11,20)
      },
      {
        width:100,
        title: '',
        dataIndex: 'receiverName'
      },
      {
        width:140,
        title: '',
        dataIndex: 'receiverMobile'
      },
      {
        title: '',
        dataIndex: 'receiverAddress'
      },
      {
        width: 100,
        title: '',
        render: (order) => {
          return (
            <span>
              {/*orderstate*/}
              <LinkButton onClick={() => this.showDetail(order)}></LinkButton>
              <LinkButton onClick={() => this.showDelivery(order)}></LinkButton>
              <LinkButton onClick={() => this.showEnd(order)}></LinkButton>
            </span>
          )
        }
      },
    ];
  }


  /*
  
   */
  getOrders = async (pageNum) => {
    this.pageNum = pageNum // pageNum, 
    this.setState({loading: true}) // loading

    const {searchContent, searchType} = this.state
    // , 
    let result
    if (searchContent) {
      result = await reqSearchOrders({pageNum, pageSize: PAGE_SIZE, searchContent, searchType})
    } else { // 
      result = await reqOrders(pageNum, PAGE_SIZE)
    }

    this.setState({loading: false}) // loading
    if (result.status === 10000) {
      // , , 
      const {total, list} = result.data
      //console.log(total,list)
      this.setState({
        total,
        orders: list
      })
    }
  }


  /**
   *  
   */
  showDetail = (order) => {
    // order ==> detail
    memoryUtils.order = order
    this.props.history.push(
      '/order/detail'
    )
  }

  /**
   * 
   */
  showDelivery = (order) =>{
    Modal.confirm({
      title: `${order.orderNo}?`,
      onOk: async () => {
        let orderNo =parseInt(order.orderNo);
        const result = await reqOrderDelivery(orderNo)
        if (result.status === 10000) {
          message.success('!')
          this.getOrders(1)
        }
      }
    })
  }

   /**
   * 
   */
    showEnd = (order) =>{
      Modal.confirm({
        title: `${order.orderNo}?`,
        onOk: async () => {
          let orderNo =parseInt(order.orderNo);
          const result = await reqOrderEnd(orderNo)
          if (result.status === 10000) {
            message.success('!')
            this.getOrders(1)
          }
        }
      })
    }


  componentWillMount () {
    this.initColumns()
  }
 
  componentDidMount () {
    this.getOrders(1)
  }

 render() {

    // 
    const {orders, total, loading, searchType, searchContent} = this.state



    const title = (
      <span>
        <Select
          value= {searchType}
          style={{width: 150}}
          onChange={value => this.setState({searchType:value})}
        >
          <Option value='orderNo'></Option>
          <Option value='receiverName'></Option>
        </Select>
        <Input
          placeholder=''
          style={{width: 150, margin: '0 15px'}}
          value={searchContent}
          onChange={event => this.setState({searchContent:event.target.value})}
        />
        <Button type='primary' onClick={() => this.getOrders(1)}></Button>
      </span>
    )

    return (
      <Card title={title}>
        <Table
          bordered
          rowKey='id'
          loading={loading}
          dataSource={orders}
          columns={this.columns}
          pagination={{
            current: this.pageNum,
            total,
            defaultPageSize: PAGE_SIZE,
            showQuickJumper: true,
            onChange: this.getOrders
          }}
        />
      </Card>
    )
  }
}

 