import React, { Component } from 'react'
import { Redirect } from 'react-router-dom'
import {
  Form,
  Icon,
  Input,
  Button,
  message
} from 'antd'
import { connect } from 'react-redux'

import './login.less'
import logo from '../../assets/images/logo.png'
import { login } from '../../redux/actions'

// const Item = Form.Item // import

/*

 */
class Login extends Component {

  
  handleSubmit = (event, user) => {

    // //console.log(user)
    // 
    event.preventDefault()

    // 
    this.props.form.validateFields(async (err, values) => {
      // 
      if (!err) {
        //console.log('ajax', values)
        // 
        // const { userName, password } = values

        // action => , 
        this.props.login(values.userName, values.password);
       
      } else {
        // //console.log('!')
      }
    });

    // form
    // const form = this.props.form
    // // 
    // const values = form.getFieldsValue()
    // //console.log('handleSubmit()', values)
  }


  render() {

    // form
    const form = this.props.form
    const { getFieldDecorator } = form;
    // , 
    const user = this.props.user
    //console.log(user)
    if (user && user.id) {
      message.success("")
      return (<Redirect to='/home' />);
    }
    return ( 
      <div className="login">
        <header className="login-header">
          <img src={logo} alt="logo" />
          <h1>SpringCloud + React</h1>
        </header>
        <section className="login-content">
          <h2></h2>
          <Form onSubmit={(event) => { this.handleSubmit(event, user) }} className="login-form">
            {/* <Form onSubmit={this.handleSubmit} className="login-form"> */}
            <Form.Item>
              {
                getFieldDecorator('userName', { // : 
                  // : 
                  rules: [
                    { required: true, whitespace: true, message: '' },
                    { min: 4, message: '4' },
                    { max: 12, message: '12' },
                    { pattern: /^[a-zA-Z0-9_]+$/, message: '' },
                  ],
                  // initialValue: 'admin', // 
                })(
                  <Input
                    prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    placeholder=""
                  />
                )
              }
            </Form.Item>
            <Form.Item>
              {
                getFieldDecorator('password', {
                  rules: [
                    { required: true, whitespace: true, message: '' },
                    { min: 4, message: '8' },
                    { max: 12, message: '16' },
                    { pattern: /^[a-zA-Z0-9_]+$/, message: '' },
                  ]
                })(
                  <Input
                    prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                    type="password"
                    placeholder=""
                  />
                )
              }

            </Form.Item>
            <Form.Item>
              <Button type="primary" htmlType="submit" className="login-form-button">
                
              </Button>
            </Form.Item>
          </Form>
        </section>
      </div>
      )
    
  }
}

const WrapLogin = Form.create()(Login)
export default connect(
  state => ({ user: state.user }),
  { login }
)(WrapLogin)

