import React, { Component } from 'react'
import {
  Card,
  Button,
  Icon,
  Table,
  message,
  Modal
} from 'antd'

import LinkButton from '../../components/link-button'
import { reqProducts, reqSearchProducts, reqUpdateStatus, reqDeleteProduct } from '../../api'
import { PAGE_SIZE } from '../../utils/constants'
import memoryUtils from "../../utils/memoryUtils";


/*
Product
 */
export default class ProductHome extends Component {

  state = {
    total: 0, // 
    products: [], // 
    loading: false, // 
    keyword: '', // 
  }

  /*
  table
   */
  initColumns = () => {
    this.columns = [
      {
        width: 50,
        title: 'id',
        dataIndex: 'id',
      },
      {
        title: '',
        dataIndex: 'name',
      },
      {
        title: '',
        dataIndex: 'updateTime',
        render: (updateTime) => updateTime.substring(0, 10) + " " + updateTime.substring(11, 20)
      },
      {
        title: '',
        dataIndex: 'price',
        render: (price) => '' + price / 100
      },
      {
        title: '',
        dataIndex: 'stock',
      },
      {
        width: 100,
        title: '',
        //dataIndex: 'status',
        render: (product) => {
         // //console.log(product)
          const { status, id } = product
          const newStatus = status === 1 ? 0 : 1
          return (
            <span>
              <span>{status === 1 ? '' : ''}</span>
              <Button
                type='primary'
                onClick={() => this.updateStatus(id, newStatus)}
              >
                {status === 1 ? '' : ''}
              </Button>
            </span>
          )
        }
      },
      {
        width: 100,
        title: '',
        render: (product) => {
          return (
            <span>
              {/*productstate*/}
              <LinkButton onClick={() => this.showDetail(product)}></LinkButton>
              <LinkButton onClick={() => this.showUpdate(product)}></LinkButton>
              <LinkButton onClick={() => this.deleteProduct(product)}></LinkButton>
            </span>
          )
        }
      },
    ];
  }

  /*
  
   */
  showDetail = (product) => {
    // product ==> detail
    memoryUtils.product = product
    this.props.history.push('/product/detail')
  }

  /*
  
   */
  showUpdate = (product) => {
    // product ==> 
    memoryUtils.product = product
    this.props.history.push('/product/update')
  }

  /*
  
   */
  getProducts = async (pageNum) => {
    this.pageNum = pageNum // pageNum, 
    this.setState({ loading: true }) // loading

    const { keyword} = this.state
    // , 
    let result
    if (keyword) {
      result = await reqSearchProducts({ pageNum, pageSize: PAGE_SIZE, keyword })
    } else { // 
      result = await reqProducts(pageNum, PAGE_SIZE)
    }

    this.setState({ loading: false }) // loading
    if (result.status === 10000) {
      // , , 
      const { total, list } = result.data
     // //console.log(total, list)
      this.setState({
        total,
        products: list
      })
    }
  }

  /*
  
   */
  updateStatus = async (ids, sellStatus) => {
    const result = await reqUpdateStatus(ids, sellStatus)
    if (result.status === 10000) {
      message.success('')
      this.getProducts(this.pageNum)
    }
  }

  /*
  
  */
  deleteProduct = (product) => {
   // //console.log(product, "=======")
    Modal.confirm({
      title: `${product.name}?`,
      onOk: async () => {
        const result = await reqDeleteProduct(product.id)
        if (result.status === 10000) {
          message.success('!')
          this.getProducts(this.pageNum)
        }
      }
    })
  }

  componentWillMount() {
    this.initColumns()
  }

  componentDidMount() {
    this.getProducts(1)
  }

  render() {

    // 
    const { products, total, loading, keyword } = this.state



    // const title = (
    //   <span>
       
    //       
        
    //     <Input
    //       placeholder=''
    //       style={{ width: 150, margin: '0 15px' }}
    //       value={keyword}
    //       onChange={event => this.setState({ keyword: event.target.value })}
    //     />
    //     <Button type='primary' onClick={() => this.getProducts(1)}></Button>
    //   </span>
    // )
    const title = (
      <span></span>
    )

    const extra = (
      <Button type='primary' onClick={() => this.props.history.push('/product/addupdate')}>
        <Icon type='plus' />
        
      </Button>
    )

    return (
      <Card title={title} extra={extra}>
        <Table
          bordered
          rowKey='id'
          loading={loading}
          dataSource={products}
          columns={this.columns}
          pagination={{
            current: this.pageNum,
            total,
            defaultPageSize: PAGE_SIZE,
            showQuickJumper: true,
            onChange: this.getProducts
          }}
        />
      </Card>
    )
  }
}