import React, {Component} from 'react'
import {
  Icon,
  Card,
  DatePicker,
  Timeline
} from 'antd'
import moment from 'moment'
import Line from './line'
import Bar from './bar'
import './home.less'

const dateFormat = 'YYYY/MM/DD'
const {RangePicker} = DatePicker

export default class Home extends Component {

  state = {
    isVisited: true
  }

  handleChange = (isVisited) => {
    return () => this.setState({isVisited})
  }

  render() {
    const {isVisited} = this.state

    return (
      <div className='home'>
        

        <Card
          className="home-content"
          title={<div className="home-menu">
            <span className={isVisited ? "home-menu-active home-menu-visited" : 'home-menu-visited'}
                  onClick={this.handleChange(true)}></span>
            <span className={isVisited ? "" : 'home-menu-active'} onClick={this.handleChange(false)}></span>
          </div>}
          extra={<RangePicker
            defaultValue={[moment('2022/01/01', dateFormat), moment('2022/06/01', dateFormat)]}
            format={dateFormat}
          />}
        >
          <Card
            className="home-table-left"
            title={isVisited ? '' : ''}
            bodyStyle={{padding: 0, height: 275}}
            extra={<Icon type="reload"/>}
          >
            {isVisited?<Bar/> :<Line/>}
          </Card>

          <Card title='' extra={<Icon type="reload"/>} className="home-table-right">
            <Timeline>
              <Timeline.Item color="green"></Timeline.Item>
              <Timeline.Item color="green">SpringCloudSpringBoot</Timeline.Item>
              <Timeline.Item color="green">postman</Timeline.Item>
              <Timeline.Item color="red">
                 <p>React + antd </p>
                
              </Timeline.Item>
              <Timeline.Item>
              <p></p>
              </Timeline.Item>
              <Timeline.Item>
                
                
                <p></p>
                <p></p>
              </Timeline.Item>
            </Timeline>
          </Card>
        </Card>
      </div>
    )
  }
}