import React, { PureComponent } from 'react'
import { Row, Col, Select, Upload, Card, Icon, Form, Input, TreeSelect,Cascader, Button, message } from 'antd';
import RichEditor from './rich-editor'
// import PicturesWall from './pictures-wall'
// import RichTextEditor from './rich-text-editor'
import LinkButton from '../../components/link-button'
import './add-product.less'
import { reqCategorys, reqCategorys2,reqAddProduct, reqUpdateProduct } from '../../api/index'
import memoryUtils from "../../utils/memoryUtils";

const { Item } = Form
const { TextArea } = Input
const Option = Select.Option;
const { TreeNode } = TreeSelect;
/*
Product
 */
class ProductUpdate extends PureComponent {


    constructor(props) {
        super(props);

        // state
        const product = memoryUtils.product  // , 
        // // 
        // this.isUpdate = !!product.id
        // //console.log(this.isUpdate)
        // (, {})
        this.product = product || {}
        //console.log(product)
        this.state = {
            id: product.id,
            // isUpdate: this.isUpdate,
            categoryList: [], //
            name: "", //
            // subtitle: '', //
            categoryId: '', //id
            tree:[],

            // subImages: [], //
            image: "",
            price: '', //
            stock: '', //
            detail: "", //
            status: 1 // 1
        }
    }

    loadCategoryList = () => {
        reqCategorys2().then(res => {
            if (res.status === 10000) {
                this.setState({ tree: res.data });
              } else {
                message.error(res.msg);
              }
        })
        // let params = {
        //     categoryId: 0
        // }
        // reqCategorys(1, 100).then(res => {
        //     let { status, msg, data } = res;
        //     if (status !== 10000) {
        //         message(msg);
        //         return
        //     }

        //     let categoryList = Array.from(data.list);
        //     let topCategoryList = categoryList.filter((c) => (c.parentId === 0))
        //     //console.log(topCategoryList)
        //     let subCategoryList = categoryList.filter((c) => (c.parentId !== 0))
        //     //console.log(subCategoryList)
        //     this.setState({
        //         categoryList
        //     })
        // })
    }

    //
    selectChange = (val) => {
        this.setState({
            categoryId: val
        })
    }
    //
    onName = (e) => {
        this.setState({
            name: e.target.value
        })
    }
    //
    // onDescription = (e) => {
    //     this.setState({
    //         subtitle: e.target.value
    //     })
    // }
    //
    onPrice = (e) => {
        this.setState({
            price: 100 * e.target.value
        })
    }
    //
    onPieces = (e) => {
        this.setState({
            stock: e.target.value
        })
    }
    //
    uploadImg = (obj) => {
        let { file } = obj;
        if (file.status === 'done') {
            let { status, data } = file.response;
            if (status === 10000) {
                //let subImages = this.state.subImages.push(data.url);
                this.setState({
                    // subImages,
                    image: data
                })
            }
        }
    }
    //
    getSubImagesString = () => {
        return this.state.subImages.map((image) => image.uri).join(',');
    }

    //
    getImageLocation = () => {
        return this.state.image;
    }

    //
    detailChange = (detail) => {
        this.setState({
            detail
        })
    }
    //
    onSubmit = () => {

        const product = memoryUtils.product  // , 
        let params = {
            name: this.state.name, //
            // subtitle    : this.state.subtitle, //
            categoryId: parseInt(this.state.categoryId, 10), //id
            // subImages   : this.getSubImagesString(), //
            image: this.state.image,
            detail: this.state.detail, //
            price: parseInt(this.state.price), //
            stock: parseInt(this.state.stock, 10), //
            status: this.state.status, // 1
        }

        //console.log(params)


        // 
        if (typeof params.name !== 'string' || params.name.length === 0) {
            message.info('');
            return;
        }

        // ID
        if (typeof params.categoryId !== 'number' || !(params.categoryId > 0)) {
            message.info('');
            return;
        }

        // 0
        if (typeof params.price !== 'number' || !(params.price >= 0)) {
            message.info('');
            return;
        }

        // 0
        if (typeof params.stock !== 'number' || !(params.stock >= 0)) {
            message.info('');
            return {
                status: false,
            }
        }
        // 
        // if(typeof params.subImages !== 'string' || !params.subImages){
        //     return {
        //         status: false,
        //         msg: ''
        //     }
        // }
        //console.log("")
        if (!params.image) {
            message.info('');
            return;
        }
        // //console.log("")
        // 
        if (!params.detail) {
            message.info('');
            return;
        }
        // //console.log(params.detail)
        ////console.log("")
        // , _id
        const id = this.state.id;
        //console.log(id)
        params = {
            id: id,
            name: this.state.name, //
            // subtitle    : this.state.subtitle, //
            categoryId: parseInt(this.state.categoryId, 10), //id
            // subImages   : this.getSubImagesString(), //
            image: this.state.image,
            detail: this.state.detail, //
            price: parseInt(this.state.price), //
            stock: parseInt(this.state.stock, 10), //
            status: this.state.status, // 1
        }
        reqUpdateProduct(params).then(res => {
            let { status, data } = res;
            //console.log(params)
            //console.log(res)
            if (status === 10000) {
                message.success(data);
                this.props.history.push('/product');
            }
        })

    }


    componentDidMount() {

        this.loadCategoryList()
    }

    render() {

        const product = memoryUtils.product  // , 
        const fileList = [];
        const uploadImg = {
            action: '/admin/upload/file', //
            //name: 'upload_file', //
            file: 'upload_file', //
            listType: 'picture',
            defaultFileList: [...fileList],
            className: 'upload-list-inline',
        };

        const title = (
            <span>
              <LinkButton>
                <Icon
                  type='arrow-left'
                  style={{marginRight: 10, fontSize: 20}}
                  onClick={() => this.props.history.goBack()}
                />
              </LinkButton>
      
              <span></span>
            </span>
          )

        return (
            <div className="add-product">
                  <Card title={title} className='product-detail'>
                <Row>
                    <Col span={5}><p className='label'></p></Col>
                    <Col span={10}><Input placeholder="" onChange={this.onName} defaultValue={product.name} /></Col>
                </Row>
                {/* <Row>
                <Col span={5}><p className='label'></p></Col>
                <Col span={10}><Input placeholder="" onChange={this.onDescription} /></Col>
            </Row> */}
                <Row>
                    <Col span={5}><p className='label'></p></Col>
                    <Col span={10}>
                        {/* <Select defaultValue="" className='select' onChange={this.selectChange} defaultValue={product.categoryId}>
                            <Option value=""></Option>
                            {
                                this.state.categoryList.length ? this.state.categoryList.map((category, index) => {
                                    return <Option value={category.id} key={index}>{category.name}</Option>
                                }) : null
                            }

                        </Select> */}
                         <TreeSelect
                            showSearch
                            style={{ width: '100%' }}
                            value={this.state.categoryId}
                            // dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                            placeholder="Please select"
                            allowClear
                            treeDefaultExpandAll
                            onChange={(value) => { this.setState({ categoryId: value }) }}
                        >
                            {
                                this.state.tree.map((item) => {
                                    if (item.childCategory.length === 0) {
                                        return (
                                            <TreeNode value={item.id} title={item.name} key={item.id} />
                                        )
                                    } else {
                                        return (
                                            <TreeNode value={item.id} title={item.name} key={item.id}>
                                                {item.childCategory.map((item2) => {
                                                    //console.log("item2:", item2);
                                                    if (item2.childCategory.length === 0) {
                                                        return (
                                                            <TreeNode value={item2.id} title={item2.name} key={item2.id} />
                                                        )
                                                    } else {
                                                        return (
                                                            <TreeNode value={item2.id} title={item2.name} key={item2.id}>
                                                                {
                                                                    item2.childCategory.map((item3) => {
                                                                        return (
                                                                            <TreeNode value={item3.id} title={item3.name} key={item3.id} />
                                                                        )
                                                                    })
                                                                }
                                                            </TreeNode>

                                                        )
                                                    }
                                                })}
                                            </TreeNode>
                                        )
                                    }
                                })
                            }
                        </TreeSelect>
                    </Col>
                </Row>
                <Row>
                    <Col span={5}><p className='label'></p></Col>
                    <Col span={10}>
                        <Input placeholder="" addonAfter="" onChange={this.onPrice} defaultValue={product.price / 100} />
                    </Col>
                </Row>
                <Row>
                    <Col span={5}><p className='label'></p></Col>
                    <Col span={10}>
                        <Input placeholder="" addonAfter="" onChange={this.onPieces} defaultValue={product.stock} />
                    </Col>
                </Row>
                <Row>
                    <Col span={5}><p className='label'></p></Col>
                    <Col span={10}>
                        <Upload {...uploadImg} onChange={this.uploadImg}>
                            <Button><Icon type="upload" /> </Button>
                        </Upload>

                    </Col>
                </Row>
                <Row>
                    <Col span={5}><p className='label'></p></Col>
                    <Col span={18}>
                        <RichEditor detailContent={this.state.detail} onChange={this.detailChange} defaultValue={product.detail}></RichEditor>
                    </Col>
                </Row>
                <Row>
                    <Col offset={5} span={10}>
                        <Button type="primary" onClick={this.onSubmit}></Button>
                    </Col>
                </Row>
                </Card>
            </div>
        )
    }

}

// export default Form.create()(ProductAddUpdate)

export default ProductUpdate;