import React, { Component } from 'react'
import {
  Card,
  Table,
  Button,
  Icon,
  message,
  Modal
} from 'antd'

import LinkButton from '../../components/link-button'
import { reqCategorys, reqUpdateCategory, reqAddCategory, reqDeleteCategory } from '../../api'
import AddForm from './add-form'
import UpdateForm from './update-form'

/*

 */
export default class Category extends Component {

  state = {
    loading: false, // 
    categorys: [], // 
    subCategorys: [], // 
    parentId: '0', // ID
    parentName: '', // 
    type: '1',
    showStatus: 0, // /, 0: , 1: , 2: 
  }

  /*
  Table
   */
  initColumns = () => {
    this.columns = [
      {
        title: 'id',
        dataIndex: 'id', // 
      },
      {
        title: '',
        dataIndex: 'name', // 
      },
      {
        title: 'id',
        dataIndex: 'parentId', // 
      },
      {
        title: '',
        dataIndex: 'type', // 
      },
      {
        title: '',
        dataIndex: 'orderNum', // 
      },
      {
        title: '',
        dataIndex: 'updateTime', // 
        render:(updateTime) => updateTime.substring(0,10)+" "+updateTime.substring(11,20)
      },
      {
        title: '',
        width: 300,
        render: (category) => ( // 
          <span>
            <LinkButton onClick={() => this.showUpdate(category)}></LinkButton>
            {/*: , */}
            {/* console.log(this.state.parentId,this.state.type) */}
            {/* {(this.state.parentId === '0' && this.state.type ==='1') ? <LinkButton onClick={() => this.showSubCategorys(category)}></LinkButton> : null} */}
            <LinkButton onClick={() => this.deleteCategory(category)}></LinkButton>
          </span>
        )
      }
    ]
  }


  /*
  /
  parentId: parentId, 
   */
  getCategorys = async (pageNum,pageSize) => {

    // , loading
    this.setState({ loading: true })
    
    // ajax, 
    const result = await reqCategorys(pageNum,pageSize)
    // , loading
    this.setState({ loading: false })
    

    if (result.status === 10000) {
      // ()
      const categorys = result.data.list;
      // //console.log(categorys);
      // //console.log(categorys.length);
    //  parentId = categorys.parentId;
     // type = categorys.type;
     
     for(var i=0;i<categorys.length-1;i++){
       //console.log(categorys[i].type,categorys[i].name,categorys[i].parentId);
       let parentId = categorys[i].parentId;
       let type = categorys[i].type;
       if (parentId === 0 && type === 1) {
        // 
        // //console.log("")
        this.setState({
          categorys
        })
        //console.log(categorys)
      } else {
        // 
        // //console.log("")
        this.setState({
          subCategorys: categorys
        })
      }

     }
      
    } else {
      message.error('')
    }
  }

  /*
  
   */
  showSubCategorys = (category) => {
    // 
    this.setState({
      parentId: category.id,
      parentName: category.name,
      type: category.type,
    }, () => { // render()
      //console.log('parentId', this.state.parentId) // '0'
      // 
      this.getCategorys(1,100)
    })
  }

  /*
  
   */
  showCategorys = () => {
    // 
    this.setState({
      parentId: '0',
      parentName: '',
      subCategorys: []
    })
  }

  /*
  : 
   */
  handleCancel = () => {
    // 
    this.form.resetFields()
    // 
    this.setState({
      showStatus: 0
    })
  }

  /*
  
   */
  showAdd = () => {
    this.setState({
      showStatus: 1
    })
  }

  /*
  
   */
  addCategory = () => {
    this.form.validateFields(async (err, values) => {
      if (!err) {
        // 
        this.setState({
          showStatus: 0
        })

        // , 
        //const { name,type,parentId, orderNum } = values
        const params = values;
        //console.log(params)
        const result = await reqAddCategory(params)
        if (result.status === 10000) {
          this.getCategorys(1,100)
          // // 
          // if (parentId === this.state.parentId) {
          //   // 
          //   this.getCategorys(1,10)
          // } 
          // else if (parentId === '0') { // , , 
          //   this.getCategorys(1,10)
          // }
        }
        // 
        this.form.resetFields()
      }
    })
  }


  /*
  
   */
  showUpdate = (category) => {
    // 
    this.category = category
    // 
    this.setState({
      showStatus: 2
    })
  }

  /*
  
   */
  updateCategory = () => {
    //console.log('updateCategory()')
    // , 
    this.form.validateFields(async (err, values) => {
      if (!err) {
        // 1. 
        this.setState({
          showStatus: 0
        })

        // 
        const id = this.category.id
        //const { name ,type,orderNum,parentId } = values
      //  //console.log(values.categoryName);
       const name = values.name;
       const type = values.type;
       const orderNum = values.orderNum;
       const parentId = values.parentId;
       const params = {id,name,type,orderNum,parentId};

        // 2. 
        const result = await reqUpdateCategory(params)
        if (result.status === 10000) {
          // 3. 
          this.getCategorys(1,100)
        }
        // 
        this.form.resetFields()
      }
    })


  }

  /**/
  deleteCategory = (Category) => {
    Modal.confirm({
      title: `${Category.name}?`,
      onOk: async () => {
        //console.log(Category.id)
        let id =parseInt(Category.id);
        
        const result = await reqDeleteCategory(id)
        if (result.status === 10000) {
          message.success('!')
          this.getCategorys(1,100)
        }
      }
    })
  }
 



  /*
  render()
   */
  componentWillMount() {
    this.initColumns()
  }

  /*
  : ajax
   */
  componentDidMount() {
    // 
    this.getCategorys(1,100)
  }

  render() {

    // 
    const { categorys, subCategorys, parentId, parentName, loading, showStatus } = this.state
    // 
    const category = this.category || {} // 

    // card
    const title = parentId === '0' ? '' : (
      <span>
        <LinkButton onClick={this.showCategorys}></LinkButton>
        <Icon type='arrow-right' style={{ marginRight: 5 }} />
        <span>{parentName}</span>
      </span>
    )
    // Card
    const extra = (
      <Button type='primary' onClick={this.showAdd}>
        <Icon type='plus' />
        
      </Button>
    )

    return (
      <Card title={title} extra={extra}>
        <Table
          bordered
          rowKey='id'
          loading={loading}
          dataSource={parentId === '0' ? categorys : subCategorys}
          columns={this.columns}
          pagination={{ defaultPageSize: 5, showQuickJumper: true }}
        />

        <Modal
          title=""
          visible={showStatus === 1}
          onOk={this.addCategory}
          onCancel={this.handleCancel}
        >
          <AddForm
            categorys={categorys}
            parentId={parentId}
            setForm={(form) => { this.form = form }}
          />
        </Modal>

        <Modal
          title=""
          visible={showStatus === 2}
          onOk={this.updateCategory}
          onCancel={this.handleCancel}
        >
          <UpdateForm
            categoryName={category.name}
            orderNum = {category.orderNum}
            type={category.type}
            parentId={category.parentId}
            setForm={(form) => { this.form = form }}
          />
        </Modal>
      </Card>
    )
  }
}